import Entity from "./entity";
import Actor from "./actor";
import Vec2 from "./vec2";
import Level from "./level";
import { overlap } from "./utils";

class Solid extends Entity {
  public collidable: boolean;

  constructor(public position: Vec2, public half: Vec2) {
    super(position, half);
    this.collidable = true;
  }

  moveX(amount: number) {
    this.xRemainder += amount;
    let move = Math.round(this.xRemainder);

    if (move !== 0) {
      const riding = this.getAllRidingActors();
      const sign = Math.sign(move);
      this.xRemainder -= move;
      this.collidable = false;

      while (move !== 0) {
        this.position.x += sign;

        if (move > 0) {
          for (const actor of Level.actors) {
            if (overlap(this, actor)) {
              // Push right
              actor.moveX(this.right() - actor.left(), actor.squish);
            } else if (riding.includes(actor)) {
              // Carry
              actor.moveX(sign, null);
            }
          }
        } else {
          for (const actor of Level.actors) {
            if (overlap(this, actor)) {
              // Push left
              actor.moveX(this.left() - actor.right(), actor.squish);
            } else if (riding.includes(actor)) {
              // Carry
              actor.moveX(sign, null);
            }
          }
        }

        move -= sign;
      }

      this.collidable = true;
    }
  }

  moveY(amount: number) {
    this.yRemainder += amount;
    let move = Math.round(this.yRemainder);

    if (move !== 0) {
      const riding = this.getAllRidingActors();
      const sign = Math.sign(move);
      this.yRemainder -= move;
      this.collidable = false;

      while (move !== 0) {
        this.position.y += sign;

        if (move > 0) {
          for (const actor of Level.actors) {
            if (overlap(this, actor)) {
              // Push top
              actor.moveY(this.bottom() - actor.top(), actor.squish);
            } else if (riding.includes(actor)) {
              // Carry
              actor.moveY(sign, null);
            }
          }
        } else {
          for (const actor of Level.actors) {
            if (overlap(this, actor)) {
              // Push bottom
              actor.moveY(this.top() - actor.bottom(), actor.squish);
            } else if (riding.includes(actor)) {
              // Carry
              actor.moveY(sign, null);
            }
          }
        }

        move -= sign;
      }

      this.collidable = true;
    }
  }

  getAllRidingActors(): Array<Actor> {
    return Level.actors.filter(actor => actor.isRiding(this));
  }
}

export default Solid;
