class Vec2 {
    constructor(public x: number, public y: number) {
        this.x = x
        this.y = y
    }

    add(other: Vec2) {
        this.x += other.x
        this.y += other.y
    }

    clone() {
        return new Vec2(this.x, this.y)
    }

    static addTwo(a: Vec2, b: Vec2): Vec2 {
        return new Vec2(a.x + b.x, a.y + b.y)
    }

    toString() {
        return `(${this.x}, ${this.y})`
    }
}

export default Vec2
