import Solid from "./solid";
import Vec2 from "./vec2";

const UPDATE_RATE = 1 / 60; // 60 frames / s

/**
 * A * ( sin ( B * ( x – C ) ) ) + D
 * A = Amplitude (Tallness) of the wave.
 * B = How many waves there are for each cycle.
 * C = How far to shift the wave’s X position.
 * D = How far to shift the wave’s Y position.
 **/

class MovingWall extends Solid {
  private moveBy: Vec2;
  private speed: number;
  private t: number;
  private prevDelta: Vec2;
  private elapsed: number;
  private direction: number;

  constructor(
    position: Vec2,
    half: Vec2,
    moveBy: Vec2,
    speed: number,
    direction: number
  ) {
    super(position, half);
    const signX = Math.sign(moveBy.x);
    const signY = Math.sign(moveBy.y);

    this.moveBy = new Vec2(
      signX * Math.abs(moveBy.x),
      signY * Math.abs(moveBy.y)
    );

    this.speed = speed;
    this.direction = direction;

    this.prevDelta = new Vec2(0, 0);
    this.t = 0;
    this.elapsed = 0;
  }

  update(dt: number) {
    this.t += dt;

    this.elapsed += dt;
    if (this.elapsed >= 1) {
      this.elapsed %= 1;
    }

    const sin = Math.sin(this.t * this.speed);
    const cos = Math.cos(this.t * this.speed);

    const x = sin * this.moveBy.x;
    const y = cos * this.moveBy.y;

    const deltaX = x - this.prevDelta.x;
    const deltaY = y - this.prevDelta.y;

    this.moveX(deltaX);
    this.moveY(deltaY);

    this.prevDelta.x = x;
    this.prevDelta.y = y;
  }
}

export default MovingWall;
